<template>
<div class="bg" id="app" @click="launchFullscreen">
  <div class="load" v-if="loading"></div>
  <div v-for="(person,index) in people" :key="person.src"  >
    <img 
    v-lazy="($env==='development' ? '': '/card') + '/static/people/' + person.src" 
    :alt="index" 
    :title="person.src"
    @click="showCard(index)"
     class="person" :style="{ transform: transform[index]}">
    <img 
    v-lazy="cardUrls[index]" 
    style="display:none">
  </div>
  <div>
    <transition name="fade">
      <div class="card" v-show="show" :style="{'background-image': `url(${cardSrc})`,'background-color': people[activeIndex].color}" @click="showDesc=true">
        <img :src="cardSrc" :alt="cardSrc" width="100%" style="z-index:-1;display:none" >
        <div class="icon" v-if="iconColor==='w'">
          <img src="./assets/icon/icon-w-2.svg" alt="音量" srcset="" @click="play" v-if="playing"> 
          <img src="./assets/icon/icon-w-1.svg" alt="音量" srcset="" @click="play" v-else> 
        </div>
        <div class="icon" v-else>
          <img src="./assets/icon/icon-b-2.svg" alt="音量" srcset="" @click="play" v-if="playing"> 
          <img src="./assets/icon/icon-b-1.svg" alt="音量" srcset="" @click="play" v-else> 
        </div>


      </div>
    </transition>
    <img @click="show=false;showDesc=false" v-show="showDesc" :src="descUrls[activeIndex]" alt="" class="card" srcset="" width="100%">
  </div>
  <video :src="audioSrc" autoplay style="display:none" ref="audio">
      您的浏览器不支持 audio 标签。
  </video>
</div>
</template>

<script>
console.log(process.env)
export default {
  name: 'App',
  data(){
    return {
      cardSrc: '',
      activeIndex: 0,
      loading: true,
      playing: false,
      audioSrc: '',
      activeTag: '',
      showDesc: false,
      show: false,
      people: [
        {
          src: '孔雀男-01.gif',
          top: '280',
          left: '200',
          color: '#074ebd'
        },
        {
          src: '食肉女-01.gif',
          top: '110',
          left: '450',
          color: '#f63068'
        },
        {
          src: '高富帅-02.gif',
          top: '280',
          left: '860',
          color: '#ffa023'
        },
        {
          src: '白富美-02.gif',
          top: '180',
          left: '1250',
          color: '#ffa023'
        },
        {
          src: '程序员-15.svg',
          top: '10',
          left: '1690',
          color: '#ffa023'
        },
        {
          src: '白骨精-03.png',
          top: '300',
          left: '2000',
          color: '#084ebd'
        },
        {
          src: '小鲜肉-05.png',
          top: '180',
          left: '2500',
          color: '#f63068'
        },
        {
          src: '食肉男-05.png',
          top: '70',
          left: '2900',
          color: '#f63068'
        }
      ]
    }
  },

  mounted(){
    //预加载图片
    let promiseArr = this.cardUrls.slice(0,2).map(url => this.loadImage(url));
    Promise.all(promiseArr).then(() => this.loading = false);
    // this.loadImage(this.baseUrl + '/static/2-人物展示/' + '孔雀男-01.jpg').then((url) => {
    //   console.log(url);
    //   this.loading = false;
    // })
    setTimeout(()=>{
      this.loading = false;
    }, 5000);
  },

  methods: {
    loadImage(url){
      return new Promise(function(resolve, reject){
        let image = new Image();
        image.src = url;
        image.onload = function(){
          resolve(url);
        }
      })
    },
    showCard(index){
      this.show = true;
      this.activeTag = this.people[index].src.split('-')[0];
      this.activeIndex = index;
      // this.people[index].transform = 'scale(2)'
      this.cardSrc = this.cardUrls[index];
      //打开现在的卡片时，加载下一个卡片
      if(index < 13){
        let nextCardSrc = this.cardUrls[index + 1];
        this.loadImage(nextCardSrc);
      }
    },
    launchFullscreen(e) {
      return;
      let element = document.documentElement;
      if(element.requestFullscreen) {
        element.requestFullscreen();
      } else if(element.mozRequestFullScreen) {
        element.mozRequestFullScreen();
      } else if(element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
      } else if(element.msRequestFullscreen) {
        element.msRequestFullscreen();
      }
    },
    play(e){
      // let audio = new Audio();
      // audio.src = '/static/audio/猪猪女孩.m4a';
      // audio.play();
      console.log('play');
      e.stopPropagation();
      let activeTag = this.activeTag;
      this.audioSrc = (this.$env==='development' ? '': '/card') + `/static/audio/${activeTag}.m4a`;
      let audio = this.$refs.audio;
      audio.addEventListener('play',() => {
        this.playing = true
      })
      audio.addEventListener('ended',() => {
        this.playing = false
      })
      this.$refs.audio.play();
    }
  },

  computed:{
    cardUrls(){
      return this.people.map(p => {
        return this.baseUrl + '/static/2-人物展示/' + p.src.replace(/(png)|(gif)/, 'jpg');
      })
    },
    descUrls(){
      return this.people.map(p => {
        return this.baseUrl + '/static/desc/' + p.src.split('-')[0] + '.jpg';
      })
    },
    iconColor(){
      if(['虎妈', '猪猪女孩', '小鲜肉'].indexOf(this.activeTag) !== -1){
        return 'b'
      }
      return 'w'
    },
    baseUrl(){
      return this.$env === 'development' ? '': '/card';
    },
    transform(){
      return this.people.map((person,index) => `translateX(${person.left || index*250}px) translateY(${person.top ||'200'}px) ${person.transform || ''}`)
    }
  }
}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}
.load{
  background-image: url('./assets/loading.jpg');
  background-size: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
}
.icon{
  position: absolute;
  top: 90%;
  left: 50%;
  z-index: 99;
  transform: translateX(-50%);
  width: 60px;
  height: 60px;
}
.card{
  position: fixed;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;

}
html,body{
  height: 100%;
  overflow-y: hidden;
}
.bgg{
  background-color: #6200c1;
  background-image: linear-gradient(90deg, #6200c1 0%, #daae3a 28%, #2632b3 57%, #acd0f4 100%);

}
.bg{
  overflow-y: hidden;
  position: relative;
  height: 100vh;
  width: 3300px;
  background-image: url('./assets/bg.svg');
  background-color: #3e0a9d;
  background-size: cover;
}
.person{
  width:280px;
  transition: .3s linear all;
  position:absolute;
}
.fade-enter-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.down-enter-active{
  transition: transform .5s;
}
.down-enter, .down-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100vh);
}
</style>



